.totalOnlineCount {
    display: none !important;
}

.desktop-container {
    /* this needs to be 100% */
    width: 100%;
}
.desktop-shortcuts-container {
    display: none; /* Use flex display */
    flex-wrap: wrap; /* Allow items to wrap to the next line */
    align-items: flex-start; /* Align items to the start of the flex line */
    justify-content: flex-start; /* Align items to the start of the main axis */
    padding: 10px;
    gap: 5px; /* Uniform gap between items */
    position: absolute;
    top: 5vh;
    overflow: auto; /* Enables scrolling if content overflows */
    width: 100%; /* Ensure the container takes up full width */
    height: 90%; /* Adjust height to prevent overflow beyond the viewport */
    margin-left: auto; /* Center the container horizontally */
    margin-right: auto; /* Center the container horizontally */
}

/* these are the shortcuts */
.desktop-shortcuts-container > div {
    text-align: center;
    font-size: 12px; /* Adjusted for better readability */
    width: 64px; /* Set a fixed width for each shortcut */
    margin: 5px; /* Add margin around each shortcut */
    display: flex; /* Use flexbox for internal alignment */
    flex-direction: column; /* Stack items vertically within each shortcut */
    justify-content: center; /* Center content vertically within each shortcut */
    align-items: center; /* Center content horizontally within each shortcut */
    user-select: none; /* Prevent text selection */
}

.bp-desktop-icon {
    width: 32px; /* Set a fixed width for the icon */
    height: 32px; /* Set a fixed height for the icon */
    color: white;
}
.bp-desktop-shortcut {
  
}

.folder-holder {
    width: 100%;
    height: 100%;
    /* beige folder */
    /* background-color: #f5f5dc; */
    position: relative;
}

.odometer {
  display: inline-flex;
  /*font-family: monospace;*/
  overflow: hidden;
  color: #00CED1;
  font-weight: bold;
  height: 1em;
}

.odometer-digit {
  color: #00CED1;
  position: relative;
  width: 0.6em;
  height: 1em;
  overflow: hidden;
}

.odometer-digit-inner {
  position: absolute;
  top: 0;
  transition: transform 0.5s ease-out;
  will-change: transform;
}

.odometer-digit-inner span {
  display: block;
  height: 1em;
  color: #00CED1;
}

@media (max-width: 767px) and (pointer: coarse) and (hover: none) {
    .desktop-shortcuts-container {
        top: 0px;
        height: calc(100vh - 128px);
    }
    .desktop-shortcuts-container img {
        width: 128px !important; 
        height: 128px !important;
    }
    .desktop-shortcuts-container .icon {
        width: 128px !important; 
        height: 128px !important;
    }
    .desktop-shortcuts-container .title {
        font-size: 32px !important;
    }
    .bp-desktop-icon {
        width: 128px !important; /* Set a fixed width for the icon */
        height: 128px !important; /* Set a fixed height for the icon */
    }

    .shortcut .title {
        font-size: 32px !important; /* Adjusted for better readability */
    }
}

.desktop-context-menu {
    display: none;
    position: absolute;
    z-index: 9999;
    background-color: var(--desktop_section-background);
    color: var(--desktop_section-color);
    border: 1px solid #a9a9a9;
    width: 200px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.desktop-context-menu ul li {
    list-style-type: none;
    padding: 5px;
    margin: 10px;
    cursor: pointer;
}

.desktop-context-menu div {
    padding: 10px;
    font-size: 14px;
    color: var(--desktop_element-color);
    cursor: pointer;
}

.desktop-context-menu div:hover {
    background-color: var(--desktop_element_hover-background);
    color: var(--desktop_element_hover-color);
}

.desktop-section {
    /* background-color: #f9f9f9; */
}

.bp-shortcut-examples-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    padding: 10px;
}

p,
.desktop-shortcuts-container-folder a,
.buddyListHolder a {
  color: var(--desktop_text-color);
  text-shadow: var(--desktop_text-text-shadow);
}

h1, h2, h3, h4, h5, h6 {
  color: var(--desktop_headers-color);
  text-shadow: var(--desktop_headers-text-shadow);
}

.desktop-section {
  background: var(--desktop_section-background);
  color: var(--desktop_section-color);
}

.desktop-overlay {
    background: var(--desktop_overlay-background);
    color: var(--desktop_overlay-color);
}

div {
    color: var(--window_content-color);
    text-shadow: var(--window_content-text-shadow);
}

button {
    user-select: none;
}


.bp-create-shortcut-container {
    padding: 20px;
}

.bp-create-shortcut-container h2 {
    font-size: 24px;
    margin-bottom: 20px;
}

.bp-form-group {
    margin-bottom: 15px;
}

.bp-form-group label {
    display: block;
    font-size: 16px;
    margin-bottom: 5px;
}

.bp-shortcut-type {
    display: flex;
    gap: 15px;
    margin-bottom: 10px;
}

.bp-shortcut-type label {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
}

.bp-shortcut-type input[type="radio"] {
    margin: 0;
}

.bp-form-group input,
.bp-form-group select,
.bp-form-group textarea {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid;
    border-radius: 4px;
}

.bp-form-group textarea {
    height: 80px;
    resize: vertical;
}

.bp-form-actions {
    display: flex;
    gap: 10px;
    justify-content: flex-end;
}

.bp-btn {
    padding: 8px 16px;
    font-size: 14px;
    border: 1px solid;
    border-radius: 4px;
    cursor: pointer;
}

.bp-btn:hover {
    opacity: 0.8;
}

/* Responsive adjustments */
@media (max-width: 480px) {
    .bp-shortcut-type {
        flex-direction: column;
        gap: 10px;
    }
}
